.header{
    width: 100%;
    height: 678px;
    background-image: url(../../Assets/img/Hero-Aria.webp);
    background-repeat: no-repeat;
    background-size: contain;
    background-position-x: right;
    .header__container{
        .header__navigetion{
            display: flex;
            flex-direction: row;
            align-items: center;
            justify-content: space-between;
            padding: 30px;
            .navigetion__menu{
                display: flex;
                flex-direction: row;
                align-items: center;
                .menu__logo{
                    margin-right: 10px;
                }
                .menu__item{
                    padding: 20px;
                    transition: all ease 0.8s;
                    &:hover{
                        transform: translatey(-6px);
                    }
                    .menu__item-link{
                        font-style: normal;
                        font-weight: normal;
                        font-size: 16px;
                        line-height: 19px;
                        color: #150C01;
                        opacity: 0.6;
                        transition: .8s ease all;
                    }
                }
                .menu__item:hover  .menu__item-link{
                    color: #DC780B;
                }
            }
            &:first-child{
                padding-left: 0;
            }
        }
        .header__bottom{
            padding: 70px 0;
            .header__bottom-title{
                width: 555px;
                font-style: normal;
                font-weight: bold;
                font-size: 48.83px;
                line-height: 58px;
                color: #150C01;
                margin-bottom: 42px;
            }
            .header-bottom-text{
                width: 555px;
                font-style: normal;
                font-weight: 500;
                font-size: 16px;
                line-height: 24px;
                color: #150C01;
                opacity: 0.7;
                margin-bottom: 50px;
            }
        }
    }
}