*{
    margin: 0;
    padding: 0;
    box-sizing: border-box;
    font-family: 'Roboto Mono';
    list-style-type: none;
    text-decoration: none;
}
.container{
    width: 1140px;
    margin: 0 auto;
}
.btn{
    background-color: #DC780B;
    font-style: normal;
    font-weight: normal;
    font-size: 16px;
    color: #FFFFFF;
    border-radius: 5px;
    border: 2px solid transparent;
    transition: all ease 1s;
    cursor: pointer;
    padding: 14px 30px;
    &:hover{
        background-color: transparent;
        color: #DC780B;
        border: 2px solid #DC780B;
    }
}
.btn__white{
    background-color: #fff;
    border: 2px solid #DC780B;
    color: #DC780B;
    &:hover{
        background-color: #DC780B;
        border: 2px solid transparent;
        color: #fff;
    }
}
.cardBtn{
    background-color: #DC780B;
    font-style: normal;
    font-weight: normal;
    font-size: 16px;
    color: #FFFFFF;
    border-radius: 5px;
    border: 2px solid transparent;
    transition: all ease 1s;
    cursor: pointer;
    padding: 5px 10px;
    &:hover{
        background-color: transparent;
        color: #DC780B;
        border: 2px solid #DC780B;
    }
}

h1,h2,h3,h4,h5{
    font-family: 'Philosopher';
}
.paragraph{
    font-style: normal;
    font-weight: 500;
    font-size: 16px;
    line-height: 19px;
    text-align: center;
    letter-spacing: 0.2em;
    text-transform: uppercase;
    color: #DC780B;
    opacity: 0.7;
    margin-bottom: 7px;   
}