.convenience{
    .container{
        .convenience__evaluation{
            display: flex;
            flex-direction: column;
            align-items: center;
            justify-content: center;
            text-align: center;
            .evaluation__title{
                font-style: normal;
                font-weight: bold;
                font-size: 31.25px;
                line-height: 35px;
                text-transform: capitalize;
                color: #150C01;
                margin-bottom: 44px;
            }
            .evaluation__social-media{
                display: flex;
                .social-media__blog{
                    width: 310px;
                    margin: 0 50px;
                    border-radius: 5px;
                    padding: 20px 0;
                    .blog__box{
                        margin: 0 auto;
                        display: flex;
                        align-items: center;
                        justify-content: center;
                        width: 88px;
                        height: 88px;
                        border-radius: 50%;
                        background-color: #ebebeb;
                        margin-bottom: 33px;
                    }
                    .blog__title{
                        font-style: normal;
                        font-weight: bold;
                        font-size: 25px;
                        line-height: 24px;
                        text-align: center;
                        color: #150C01;
                        margin-bottom: 15px;
                    }
                    .blog__text{
                        font-style: normal;
                        font-weight: normal;
                        font-size: 16px;
                        line-height: 24px;
                    }
                }
            }
        }
        .convenience__about{
            display: flex;
            align-items: center;
            justify-content: space-between;
            padding: 100px 0;
            position: relative;
            &::before{
                content: "";
                position: absolute;
                width: 400px;
                height: 120%;
                background-image: url(../../Assets/img/svg.svg);
                background-position: left;
                background-size: cover;
                background-repeat: no-repeat;
                left: -100px;
            }
            .about-left{
                width: 50%;
                z-index: 1;
            }
            .abaut-right{
                padding-left: 40px;
                width: 50%;
                .about-right__paragraph{
                    text-align: start;
                    margin-bottom: 10px;
                }
                .about-right__title{
                    width: 470px;
                    font-style: normal;
                    font-weight: bold;
                    font-size: 31.25px;
                    line-height: 41px;
                    color: #150C01;
                    margin-bottom: 30px;
                }
                .about-right__text{
                    font-style: normal;
                    font-weight: normal;
                    font-size: 16px;
                    line-height: 24px;
                    color: #150C01;
                    margin-bottom: 40px;
                }
            }
        }
    }
}
.dietary-foods{
    padding-bottom: 140px;
    .dietary-foods__container{
        display: flex;
        flex-direction: column;
        align-items: center;
        justify-content: center;
        text-align: center;
        .dietary-foods__title{
            margin-top: 10px;
            margin-bottom: 25px;
            font-style: normal;
            font-weight: bold;
            font-size: 31.25px;
            line-height: 24px;
            display: flex;
            align-items: center;
            color: #150C01;
        }
        .dietary-foods__text{
            font-style: normal;
            font-weight: normal;
            font-size: 16px;
            line-height: 24px;
            display: flex;
            align-items: center;
            text-align: center;
            color: #150C01;
            opacity: 0.7;
            width: 780px;
            letter-spacing: -1.5px;
            margin-bottom: 40px;
        }
        .dietary-foods__cards{
            display: flex;
            flex-wrap: wrap;
            margin-bottom: 50px;
            .cards__card{
                width: 30%;
                height: 446px;
                margin: 18px;
                border-top-left-radius: 25%;
                border: 2px solid #ebebeb;
                .card-image{
                    width: 100%;
                    height: 50%;
                    border-top-left-radius: 25%;
                    border-bottom-right-radius: 25%;
                }
                .card__flex{
                    display: flex;
                    flex-direction: row;
                    align-items: center;
                    justify-content: space-between;
                    padding: 22px;
                    .card__flex-title{
                        font-style: normal;
                        font-weight: bold;
                        font-size: 25px;
                        line-height: 24px;
                        display: flex;
                        align-items: center;
                        color: #150C01;
                    }
                }
                .card__text{
                    font-style: normal;
                    font-weight: normal;
                    font-size: 16px;
                    line-height: 24px;
                    display: flex;
                    text-align: start;
                    color: #150C01;
                    padding: 0 22px;
                    letter-spacing: -1.5px;
                    margin-bottom: 30px;
                }
                .card__box{
                    display: flex;
                    align-items: center;
                    justify-content: space-between;
                    padding: 0 22px;
                    .card__box-star{
                        display: flex;
                        flex-direction: row;
                        align-items: center;
                        color:#DC780B;
                        cursor: pointer;
                        .fas{
                            &:hover{
                                color: gray;
                            }
                        }
                        .fas:last-child{
                            color: gray;
                        }
                    }
                }
            }
        }
    }
}
.services{
    padding: 100px 0;
    background-color: #FBF1E7;
    margin-bottom: 80px;
    .container{
        display: flex;
        align-items: center;
        justify-content: center;
        padding: 0 60px;
        .services__left{
            width: 70%;
            .paragraph{
                text-align: start;
                margin-bottom: 10px;
            }
            .services__left-title{
                width: 555px;
                font-style: normal;
                font-weight: bold;
                font-size: 31.25px;
                line-height: 41px;
                color: #150C01;
                margin-bottom: 30px;
            }
            .sevices__left-text{
                width: 555px;
                font-style: normal;
                font-weight: normal;
                font-size: 16px;
                line-height: 24px;
                color: #150C01;
                opacity: 0.7;
                margin-bottom: 40px;
            }
        }
        .services__right{
            width: 30%;
            position: relative;
            .services__right-img{
                position: absolute;
                top: -335px;
            }
        }
    }
}
.section{
    padding: 100px 0;
    .dietary-foods__title{
        margin-top: 15px;
        margin-bottom: 25px;
        font-style: normal;
        font-weight: bold;
        font-size: 31.25px;
        line-height: 24px;
        text-align: center;
        color: #150C01;
    }
    .dietary-foods__text{
        font-style: normal;
        font-weight: normal;
        font-size: 16px;
        line-height: 24px;
        text-align: center;
        transform: translateX(195px);
        color: #150C01;
        opacity: 0.7;
        width: 780px;
        letter-spacing: -1.5px;
        margin-bottom: 73px;
    }
    .section__carusel{
        display: flex;
        align-items: center;
        flex-direction: column;
        text-align: center;
        margin: 0 auto;
        margin-top: 20px;
        .carusel__img{
            border-radius: 50%;
            margin-bottom: 40px;
        }
        .carusel__text{
            width: 670px;
            font-style: 16px;
            line-height: 25px;
            margin-bottom: 20px;
        }
        .carusel-star{
            margin-bottom: 40px;
            display: flex;
            flex-direction: row;
            align-items: center;
            color:#DC780B;
            cursor: pointer;
            .fas{
                &:hover{
                    color: gray;
                }
            }
            .fas:last-child{
                color: gray;
            }
        }
        .carusel__title{
            font-size: 16px;
            text-transform: uppercase;
            margin-bottom: 10px;
        }
        .carusel__subTitle{
            font-size: 12px;
            color: #727272;
        }
    }
}
.contact{
    width: 100%;
    background-color: #FBF1E7;
    height: 500px;
    .paragraph{
        padding-top: 115px;
        padding-bottom: 10px;
    }
    .dietary-foods__title{
        width: 749px;
        margin: 0 auto;
        font-style: normal;
        font-weight: bold;
        font-size: 31.25px;
        line-height: 27px;
        text-align: center;
        color: #150C01;
    }
    .contact__recister{
        display: flex;
        align-items: center;
        margin: 0 auto;
        width: 750px;
        height: 60px;
        margin-top: 75px;
        border-radius: 10px;
        background-color: #fff;
        .input{
            width: 80%;
            height: 100%;
            border: none;
            outline: none;
            border-top-left-radius: 10px;
            border-bottom-left-radius: 10px;
            padding-left: 15px;
        }
        .contact_btn{
            width: 20%;
            height: 100%;
            background-color: #DC780B;
            color: #fff;
            outline: none;
            border: none;
            border-radius: 10px;
            cursor: pointer;
            font-size: 15px;
            font-weight: bold;
            text-transform: uppercase;
            border: 2px solid transparent;
            transition: 1s;
            &:hover{
                border: 2px solid #DC780B;
                background-color: #fff;
                color: #DC780B;
            }
        }
    }
}