.footer{
    width: 100%;
    padding: 105px;
    background-color:#000;
    .footer__blog{
        display: flex;
        justify-content: space-between;
        .image{
            width: 60px;
            height: 60px;
            margin-bottom: 25px;
        }
        .footer__menu-item{
            color: #fff;
            font-family: 'Roboto Mono';
            font-size: 14px;
            margin-bottom: 10px;
            font-weight: 400;
            opacity: 70%;
            a{
                color: #fff;
                opacity: 70%;
                &:hover{
                    color: #fff;
                    opacity: 100%;
                }
            }
        }
        .footer__menu-title{
            margin-bottom: 30px;
            a{
                color: #fff;
                opacity: 70%;
                transition: all 1s ease;
                &:hover{
                    color: #DC780B;
                }
            }
        }
        .footer__logo{
            margin-top: 30px;
        }
    }
}